<template>
  <div class="paging">
      <div class="page-box">
          <div class="prev-btn arr-btn">
              <a>
                  <font-awesome-icon :icon="['fas','sort-up']"/>
              </a>
          </div>
          <div class="num-btn">
              <ul class="num">
                  <li @click="sendPage(page)" :class="{active : page === pageSetting.currentPage}" v-for="page in pageSetting.list" :key="page" >
                      {{page}}
                  </li>
              </ul>
          </div>
          <div class="next-btn arr-btn">
              <a href="">
                  <font-awesome-icon :icon="['fas','sort-down']"/>
              </a>
          </div>
      </div>
  </div>
</template>

<script>
export default {
    props:["pageSetting"],

    methods: {
        sendPage(page){
            this.$emit("paging", page)
        }
    },

}
</script>

<style lang="scss">
@import '@/assets/css/common/common.scss'

</style>