                    url: 'https://picsum.photos/id/209/200'
                }
            ],



            pageArray:[],

            // 전체 데이터의 개수
            total: 23,

            // 현재 페이지의 디폴트 값
            page: 1,

            // 한 페이지 당 나타날 데이터의 개수
            limit: 15,

            // 페이지 번호 리스트에서 한번에 보여질 개수
            block: 10,

        }
    },
    components:{
        Paging
    },

    mounted() {
        this.pagingMethod(this.page)
    },

    methods: {

        // 페이지 클릭시 페이지 이동
        pagingMethod(page){

            

            // 인자 page 는 하위 컴포넌트인 paging에서 paging이라는 이벤트를 $emit으로 받는 값
            this.pageArray = this.contsposter.slice(
                (page - 1) * this.limit,
                page * this.limit
            )
            this.page = page

            // 인자로 받은 page 값을 넘김
            this.pageDataSetting(this.total, this.limit, this.block, page)
        },

        // 페이지 이동 및 block 단위로 보여주도록 하는 함수
        pageDataSetting(total,limit,block,page){


            // 전체 데이터를 나눈 뒤 나머지 값이 올림이 되도록 함
            const totalPage = Math.ceil(total / limit)

            // 현제 페이지
            let currentPage = page


            // 이전, 다음 버튼 클릭시 1씩 커지거나 작아지도록
            const first = 
                currentPage > 1 ? parseInt(currentPage, 10) - parseInt(1,10) : null
            const end =
                totalPage !== currentPage
                ? parseInt(currentPage, 10) + parseInt(1,10)
                : null

            
            // 페이지 리스트를 block 단위로 표시해주기 위함
            let startIndex = (Math.ceil(currentPage / block) - 1) * block + 1
            let endIndex = startIndex + block > totalPage ? totalPage : startIndex + block - 1
            let list = []

            for(let index = startIndex; index <= endIndex; index++){
                list.push(index)
            }

            // 값을 paging.vue에 전달
            return {first, end, list, currentPage}

        }
    },
    

}
</script>

